* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: 'Kanit', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #2d2a4a;
  text-rendering: optimizeLegibility;
  background-color: #fff;
  overflow-x: hidden;
}

h1 {
  font-weight: 300;
  font-size: 200%;
}

h2 {
  font-weight: 400;
  font-size: 180%;
}

h3 {
  font-weight: 300;
  font-size: 150%;
}