.contain {
  min-height: calc(100vh - 54px - 64px - 20px);
}

.col4 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.showData {
  padding: 30px 10px 10px 10px;
}

.logoPic {
  height: 80px;
}

.footer {
  width: 100%;
  padding: 20px 0;
}

/* Big table to 1200px (width smaller that the 1140px row) */
@media only screen and (max-width: 1200px) {
  .col4 {
    grid-template-columns: 1fr 1fr;
  }
}

/* Smaill table to big tablets: from 767px to 1023px */
@media only screen and (max-width: 1023px) {
  .col4 {
    grid-template-columns: 1fr;
  }

  .footerLayout {
    display: grid;
    grid-template-columns: 1fr;
  }

  .split {
    display: none;
  }
}