.nav {
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.navLayout {
    display: grid;
    grid-template-columns: 1fr auto;
}

/* ---------------------------------- */
/* LOGO */
/* ---------------------------------- */

.logoPic {
    height: 25px;
    position: relative;
    top: 10px;
}

.logoText {
    position: relative;
    top: 5px;
    margin-left: 10px;
}

/* ---------------------------------- */
/* MAIN MENU */
/* ---------------------------------- */

.menuList {
    list-style: none;
}

/* Smaill phone to smaill tablets: from 481px to 767px */
@media only screen and (max-width: 767px) {
    .menuList {
        display: none;
    }

    .navLayout {
        padding: 10px 0;
    }

    .logoPic {
        position: static;
    }

    .logoText {
        position: static;
    }
}